import { Component, OnInit, Input,OnChanges, Output, EventEmitter} from '@angular/core';


@Component({
  selector: 'app-list-item',
  templateUrl: './list-item.component.html',
  styleUrls: ['./list-item.component.css']
})
export class ListItemComponent implements OnInit {

  constructor() { }

  ngOnInit() {
   
    this.value2=this.items;
  }

  ngOnchanges(){
   
  }
  @Input() data;
  @Input() items;
  @Input() countz;
  value2;
  @Output() countzChange = new EventEmitter();
  gg=false;
  cc=true;
  myfun(){
    this.data.splice(this.data.indexOf(this.items),1)
   
   
   this.countz = this.data.length;
   console.log(this.countz);
    this.countzChange.emit(this.countz);
    
  }  
  myf(){
  
    this.gg=true;
    this.cc=false;
  }

  ff(value2){
    let a = this.data.indexOf(this.items);
    this.data.splice(a,1,value2);
    
  
    this.gg=false;
    this.cc=true;
  }
}



