import { Component, OnInit, Input ,OnChanges,Output,EventEmitter,AfterViewInit,ElementRef} from '@angular/core';

@Component({
  selector: 'app-todolist',
  templateUrl: './todolist.component.html',
  styleUrls: ['./todolist.component.css']
})
export class TodolistComponent implements OnInit{

  constructor(private elementRef:ElementRef) { }
  ngAfterViewInit() {
  this.elementRef.nativeElement.querySelector('input').addEventListener('keyup', this.onEnter.bind(this));
  }
  onEnter(event) {
    event.preventDefault();
  
  if (event.keyCode === 13) {
   
    document.querySelector("button").click();
  }
}
  ngOnInit(){   
    this.countz = this.datas.length;

  }

  name="";
  @Input() datas;
  countz;
  additem(){
    this.datas.push(this.name);    
    this.countz=this.datas.length;
   this.name=""
  }


}



